<template>
  <div style="max-width: 100%;box-sizing: border-box" class="getMedicineRecordDetail">
    <myHeader
      :isBack="true"
      backPath="/getMedicineRecord"
      title="领药详情">
    </myHeader>
    <div class="seat-head"></div>
    <div class="list-block">
      <mt-cell title="批次号：">{{medicineObj.batchNo}}</mt-cell>
      <mt-cell title="申请日期：">{{medicineObj.applydate}}</mt-cell>
      <mt-cell title="领药原因：">{{medicineObj.recipientReason}}</mt-cell>
      <mt-cell title="语音说话：">
        <span @click="broadcastStop" class="pause-btn" v-show="medicineObj.voiceUrl">
          <img src="@/assets/pause-icon.png">
        </span>
        <span @click="broadcastStart" class="start-btn" v-show="medicineObj.voiceUrl">
          <img src="@/assets/righticon.png">
        </span>
      </mt-cell>
    </div>
    
    <div class="select-type">
      <div class="title">
        是否委托他人领取
      </div>
      
      <div class="radio-select-box">
        <div class="radio-item">
          <span>否</span>
          <img v-if="!medicineObj.isEntrust" src="../../../assets/select-icon.png">
          <img v-else src="../../../assets/noSelect-icon.png">
        </div>
        <div class="radio-item">
          <span>是</span>
          <img v-if="medicineObj.isEntrust" src="../../../assets/select-icon.png">
          <img v-else src="../../../assets/noSelect-icon.png">
        </div>
      </div>
      
      <div v-show="medicineObj.isEntrust">
        <mt-cell title="委托领取人姓名：">{{medicineObj.entrustName}}</mt-cell>
        <mt-cell title="委托领取人身份证号：">{{medicineObj.entrustIdcard}}</mt-cell>
      </div>
    
    </div>
    
    <div class="medicine-apply-detail" v-if="dsStatus===30">
      <div class="top">
        <span>实际领用明细</span>
      </div>
      <div class="details-container" v-for="(item,index) in medicineObj.actEntrys" :key="index">
        <div class="detail-item">
          <div class="left">
            <span>兽药名称：</span>
            <span>{{item.materialName}}</span>
          </div>
        </div>
         <div class="detail-item">
          <div class="left">
            <span>数量：</span>
            <span>{{item.num}}</span>
          </div>
        </div>
        <div class="detail-item">
          <div class="left">
            <span>兽药单价：</span>
            <span>{{item.price}}</span>
          </div>
          <div class="right">
            <span>金额：</span>
            <span>{{item.amount}}</span>
          </div>
        </div>
      </div>
    </div>
    
    <Signature v-show="isShowSignature" ref="signature"></Signature>
    
    <SubmitBtn v-if="recordState != 2 && dsStatus==30"
               @click.native="submitData()"></SubmitBtn>
    
    <div style="visibility: hidden;">
      <audio controls ref="audioS" :src="medicineObj.voiceUrl"></audio>
    </div>
  
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'
  import myHeader from '@/components/Header'
  import Signature from "@/components/Signature";
  import SubmitBtn from "@/components/SubmitBtn";
  import {queryDrugApplyInfoById, saveSignerDrugApply} from '@/api/materialApply'
  import appConfig from '@/config/appConfig'
  import {mapGetters} from 'vuex'
  import {getParam} from '@/utils/wxUtil'
  import {Indicator} from 'mint-ui'
  
  export default {
    name: 'getMedicineRecordDetail',
    data() {
      return {
        medicineObj: {
          batchNo: '',
          applydate: '',
          recipientReason: '',
          voiceUrl: '',
          entrustName: '',
          entrustIdcard: '',
          isEntrust: false,
          entrys: [],
          actEntrys: []
        },
        rcordId: '',
        recordState: '',
        dsStatus: '',
        signerImgs: '',
        isShowSignature: true
      }
    },
    components: {
      SubmitBtn,
      Signature,
      myHeader
    },
    created() {
      this._queryDrugApplyInfoById();
      // this.rcordId = this.$route.params.rcordId;
      this.rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
      
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    },
    mounted() {
      this.initWxConfig();
    },
    computed: {
      ...mapGetters([
        'signature'
      ])
    },
    watch: {
      recordState(newName, oldName) {
        // 已完成签名，只需要展示签名图片，不能编辑
        if (this.recordState == 2 &&  this.dsStatus == 30) {
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.fromDataURL(this.signerImgs);
            this.$refs.signature.disabled = true;
          });
        }
    
        if (this.dsStatus == 30 && this.recordState != 2) { // 已经审核，可以签字，可以编辑
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = false;
          });
        }
  
        if (this.recordState == 3 || this.dsStatus != 30) {  // 没有审核或者已经取消
          this.isShowSignature = false;
        }
    
      }
    },
    methods: {
      initWxConfig() {
        wx.config({
          debug: false, //调试微信接口，上线可以关闭
          appId: this.signature.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
          timestamp: this.signature.timestamp, // 必填，生成签名的时间戳
          nonceStr: this.signature.nonceStr, // 必填，生成签名的随机串
          signature: this.signature.signature,// 必填，签名，见附录1
          jsApiList: [
            'startRecord',
            'stopRecord',
            'onVoiceRecordEnd',
            'playVoice',
            'pauseVoice',
            'stopVoice',
            'onVoicePlayEnd',
            'uploadVoice'
          ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
      },
      broadcastStart() {
        let vm = this;
        if (window.WeixinJSBridge) {
          window.WeixinJSBridge.invoke('getNetworkType', {}, (res) => {
            vm.$refs.audioS.load();
            setTimeout(() => {
              vm.$refs.audioS.play()
            }, 5);
          })
        } else {
          document.addEventListener('WeixinJSBridgeReady', () => {
            window.WeixinJSBridge.invoke('getNetworkType', {}, (res) => {
              vm.$refs.audioS.load();
              
              setTimeout(() => {
                vm.$refs.audioS.play()
              }, 5);
            })
          }, false);
        }
      },
      broadcastStop() {
        
        let vm = this;
        if (window.WeixinJSBridge) {
          window.WeixinJSBridge.invoke('getNetworkType', {}, (res) => {
            vm.$refs.audioS.load();
            setTimeout(() => {
              vm.$refs.audioS.pause();
            }, 5);
          })
        } else {
          document.addEventListener('WeixinJSBridgeReady', () => {
            window.WeixinJSBridge.invoke('getNetworkType', {}, (res) => {
              vm.$refs.audioS.load();
              
              setTimeout(() => {
                vm.$refs.audioS.pause();
              }, 5);
            })
          }, false);
        }
      },
      _queryDrugApplyInfoById() {
        // const rcordId = this.$route.params.rcordId;
        const rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
        queryDrugApplyInfoById(rcordId).then(res => {
          Indicator.close();
          if (res.data) {
            let data = res.data;
            data.state ? this.recordState = data.state : null;
            data.dsStatus ? this.dsStatus = data.dsStatus : null;
            data.signerUrl.length ? this.signerImgs = data.signerUrl[0].img : null;
  
  
            let newEntrysList = [];
            let newActEntrysList = [];
  
            if (data.entrys && data.entrys.length) {
              newEntrysList = data.entrys.map(item => {
                return {
                  materialName: item.materialName,
                  price: item.price,
                  amount: item.amount,
                  num: item.num
                }
              });
            }
  
            if (data.actEntrys && data.actEntrys.length) {
              newActEntrysList = data.actEntrys.map(item => {
                return {
                  materialName: item.materialName,
                  price: item.price,
                  amount: item.amount,
                  num: item.num
                }
              });
            }
            
            this.medicineObj = {
              batchNo: data.batchNo,
              applydate: data.applydate,
              recipientReason: data.recipientReason ? appConfig.recipientReason[data.recipientReason] : '',
              voiceUrl: data.voiceUrl.length > 0 ? data.voiceUrl[0].img : '',
              entrustName: data.entrustName,
              entrustIdcard: data.entrustIdcard,
              isEntrust: data.isEntrust,
              entrys: newEntrysList,
              actEntrys: newActEntrysList
            };
            
          }
          
        }).catch(err => {
          Indicator.close();
        })
      },
      submitData() {
        let signerImg = this.$refs.signature.save();
        
        const data = {
          rcordId: this.rcordId,
          fileEntry: {
            imgContent: signerImg,
            imgType: 'png'
          }
        };
        
        saveSignerDrugApply(data).then(res => {
          this.$toast({
            message: '签字确认成功！',
            duration: 1000
          });
          this.$router.push('/getMedicineRecord');
        });
        
      }
    }
  }
</script>

<style lang="scss">
  .getMedicineRecordDetail {
    min-height: 100vh;
    background-color: #F8F8F8;
    
    .head {
      background-color: #FFF;
    }
    
    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;
      
      .pause-btn {
        display: inline-block;
        padding: 0 0.25rem;
        
        img {
          width: .16rem;
        }
      }
      
      .start-btn {
        display: inline-block;
        padding: 0 0.25rem;
      }
    }
    
    .select-type {
      background-color: #fff;
      margin-top: .1rem;
      font-size: .16rem;
      
      .title {
        height: .45rem;
        padding: .1rem .15rem;
        border-bottom: 1px solid #dddddd;
      }
      
      .radio-select-box {
        padding-left: .15rem;
        border-bottom: 1px solid #dddddd;
        
        .radio-item {
          display: flex;
          justify-content: space-between;
          height: .52rem;
          line-height: .52rem;
          padding-left: .05rem;
          padding-right: .15rem;
          
          &:first-child {
            border-bottom: 1px solid #dddddd;
          }
          
          img {
            align-self: center;
          }
        }
      }
      
    }
    
    .medicine-apply-detail {
      font-size: .16rem;
      margin-top: .1rem;
      
      .top {
        background-color: #fff;
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .details-container {
        background-color: #fff;
        margin-bottom: .05rem;
        padding-left: .2rem;
        border-bottom: 1px solid #EBEBEB;
        
        .detail-item {
          padding-right: .15rem;
          font-size: .14rem;
          display: flex;
          height: .54rem;
          justify-content: space-between;
          line-height: .54rem;
          border-bottom: 1px solid #EBEBEB;
          
          .left {
            width: 60%;
            
            .tip-text {
              color: #888;
            }
          }
          
          .right {
            width: 40%;
            padding-left: 2.5%;
          }
          
          &:last-child {
            border-bottom: 0;
          }
        }
      }
      
    }
  }
</style>



